/* Center.js */
.center {
  position: relative;
  height: 100%;
  width: 100%;
}
.center .title{
  font-size: 24px;
  text-align: center;

  margin-top: 5rem;
}
.center .border-wrapper {
  position: relative;
  top: 150px;
  left: calc(100% / 3);

  max-width: 800px;
  max-height: 800px;
}
.center .border-wrapper::after{
  content: '';
  width: 100%;
  height: 100%;

  border: 1px solid #fff;
  position: absolute;
  top: 0;
  left: 0;

  border-radius: 999px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  opacity: 1;
  
  -webkit-animation: circlePulse 2s ease infinite;
  
          animation: circlePulse 2s ease infinite;
}
.center img {
  width: 100%;
  height: auto;
  max-width: 50em;
}

/* Keyframes */
@-webkit-keyframes circlePulse {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}
@keyframes circlePulse {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: 0;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

